import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, combineReducers, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';

import counterReducer from './store/reducers/counter';
import resultReducer from './store/reducers/result';

import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

const rootReducer = combineReducers({
    global_counter: counterReducer,
    global_result: resultReducer,
});

/*  middleware example. middleware is a function.
    that get executed between action reach the reducer.
    middleware function returns another function wich receives next as argument,
    and return another function with receives action as an argument */
const logger = store => {
    return next => {
        return action => {
            console.log('[ Middleware ] Dispatching', action);
            const result = next(action);
            console.log('[ Middleware ] next state', store.getState())
            return result;
        }
    }
}

/* logger and thunk are both middleware function */
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const store = createStore(rootReducer, composeEnhancers(applyMiddleware(logger, thunk)));

ReactDOM.render(<Provider store={store}><App /></Provider>, document.getElementById('root'));
registerServiceWorker();



