import * as actionTypes from './actionTypes';

/* actionCreators */
export const saveResult = ( res ) => {
    /*
      example of advantage usage
      const updatedResult = 'updated result * 2: ' + res * 2;
    */
    return {
        type: actionTypes.STORE_RESULT,
        result: res
    }
}

/* redux thunk allows to use this async inside action */
export const storeResult = (res) => {
    return (dispath) => {
        setTimeout(() => {            
            dispath(saveResult(res));
        }, 2000);
    }; 
};

