import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../utility';

const initialState = {    
    result: [],
}

const deleteResult = (state, action) => {
    /* filter() returns new array */            
    const updatedResults = state.result.filter(result => result.id !== action.elementID);
    return updateObject(state, {result: updatedResults});
};

const reducer = (state = initialState, action) => {
    switch (action.type) {        
        case actionTypes.STORE_RESULT:
            /* .push() manipulate old array. .concat() returns a new array */
            /* can also change returning value like this:
               result: state.result.concat({id: new Date(), value: action.result * 2}) */
            return updateObject(state, {result: state.result.concat({id: new Date(), value: action.result})});            
        case actionTypes.DELETE_RESULT:
            /* first way */
            // const id = 2;
            // const newArray = [...state.results];
            // newArray.splice(id, 1);            
            return deleteResult(state, action);
        default:
            return state;   
    }    
};

export default reducer;


